/*Min Width && Max Width*/

$index: 50;
@while $index <=600 {
    .min-w#{$index} {
        min-width: #{$index}px !important;
    }
    .max-w#{$index} {
        max-width: #{$index}px !important;
    }
    .w#{$index} {
        width: #{$index}px !important;
    }
    $index: ($index + 10);
}

/*Padding*/
.pt-10{
    padding-top: 10px;
}
.pb-10{
    padding-bottom: 10px;
}
.pt-12{
    padding-top: 12px;
}
.pb-12{
    padding-bottom: 12px;
}

/*Margin*/

/*Font Size*/
.fs-12{
    font-size: px_to_rem(12px) !important;
}

.fs-16{
    font-size: px_to_rem(16px) !important;
}

.fs-18{
    font-size: px_to_rem(18px) !important;
}

.fs-20{
    font-size: px_to_rem(20px) !important;
}

.fs-22{
    font-size: px_to_rem(22px) !important;
}

.fs-24{
    font-size: px_to_rem(24px) !important;
}

/*Color Themes*/
.text-green{
    color: var(--text-green);
}
.text-content{
    color: var(--text-content);
}
.text-content-2{
    color: var(--text-content-2);
}
.text-link{
    color: var(--text-link);
}

/*Flexbox*/
.flex-1{
    flex: 1;
}

/*Label Template*/
.label-themes{
    display: inline-block;
    padding: 4px 10px;
    border-radius: 8px;
    font-weight: 600;
    line-height: 20px;
    letter-spacing: 0.07px;
    &.label-success{
        color: #229A16;
        background: rgba(84, 214, 44, 0.16);
    }
    &.label-danger{
        color: #B72136;
        background-color: rgba(255, 72, 66, 0.16);
    }
    &.label-warning{
        color: #B78103;
        background: rgba(255, 193, 7, 0.16);
    }
    &.label-info{
        color: #1939B7;
        background: rgba(51, 102, 255, 0.16);
    }
    &.label-purple{
        color: #8A07AB;
        background: rgba(138, 7, 171, 0.10);
    }

    /*Notification*/
    &.label-notify{
        border-radius: 38px;
        font-size: px_to_rem(12px);
        line-height: 18px;
        padding: 2px 6px;
        &.label-success{
            color: #007B55;
            background: rgba(0, 171, 85, 0.16);
        }
    }
}

/*Badge Template*/
.badge{
    &.badge-themes{
        font-family: 'Public Sans';
        font-weight: 600;
        line-height: 20px;
        letter-spacing: 0.07px;
        color: var(--text-content);
        padding: 4px 10px;
        border-radius: 8px;
        background: rgba(145, 158, 171, 0.16);
    }
}


.icon-circle{
    padding: 8px;
    border-radius: 50%;
    width: 32px;
    height: 32px;
    [class*="icon"]{
        font-size: 16px;
    }
    &.bg-main{
        background: var(--btn-main);
        color: var(--text-white);
    }
}