/*Box Layer Right*/
.box-layer-right{
    &.active{
        .layer-panel {
            right: 0;
        }
        .layer-backdrop {
            opacity: 1;
            visibility: visible;
        }
    }
    .content-body{
        max-height: calc(100vh - 194px);
        overflow-y: auto;
    }
}
.layer-backdrop {
    background: rgba(0, 0, 0, 0.5);
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 1021;
    opacity: 0;
    visibility: hidden;
    transition: all .8s ease;
}

.layer-panel {
    background-color: white;
    position: fixed;
    right: -100%;
    top: 0;
    height: 100vh;
    z-index: 1021;
    max-width: 1290px;
    width: 100%;
    -webkit-box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.4);
    box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.4);
    transition: all 1.1s ease;
}