/*Logo*/
.logo{
    width: 74px;
    flex-shrink: 0;
}

.navbar-nav{
    .avatar{
        min-width: 32px;
        height: 32px;
    }
    .nav-item {
        &.active{
            background: var(--bg-themes);
        }
    }
}

.nav-item {
    .indicator{
        background: #FF5630;
    }
}


.date-time{
    color: #919EAB;
}

.text-notify{
    line-height: 22px;
}

.campaign-header{
    position: relative;
    .btn-campaign{
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        @media screen and (max-width: 1199px) {
            position: static;
            transform: none;
        }
    }
    .list-campaign{
        display: flex;
        align-items: center;
        @media screen and (max-width: 767px) {
            width: 100%;
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 20px;
            margin-bottom: 16px;
        }
        li{
            padding: 6px 16px;
            border-radius: 8px;
            color: var(--text-content);
            a{
                display: flex;
                align-items: center;
            }
            .icon{
                display: flex;
                margin-right: 8px;
                font-size: px_to_rem(20px);
            }
            &.active{
                background: var(--bg-hover);
                box-shadow: 0px 8px 16px 0px rgba(145, 158, 171, 0.12);
                color: #454f5b;
            }
        }
    }
}

.dropdown-menu-footer{
    font-size: px_to_rem(14px);
    font-weight: 700;
    line-height: 24px; 
    color: var(--text-main);
}

.dropdown {
    .dropdown-menu-header{
        padding: 16px 20px;
        .title-header{
            font-size: px_to_rem(16px);
            font-weight: 600;
            line-height: 24px; 
        }
        .title-notify{
            line-height: 22px; 
        }
        .icon-check-notify{
            padding: 8px;
            font-size: 24px;
            color: var(--btn-main);
        }
    }
    .dropdown-menu{
        min-width: 22.5rem;
        border: 0;
        border-radius: 8px;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
        z-index: 1021;
        &.user{
            min-width: 15rem;
            padding: 0;
            a{
                font-weight: 600;
                line-height: 20px; 
                letter-spacing: 0.07px;
                color: #4A4C56;
                padding: 12px;
                display: flex;
                align-items: center;
                [class*="icon"],
                svg{
                    width: 18px;
                    text-align: center;
                }
                &:active{
                    background-color: var(--bs-dropdown-link-hover-bg);
                }
            }
            .balance{
                .number{
                    font-size: 14px;
                    color: var(--btn-main);
                }
            }
        }
        &.menu-bugger{
            border-radius: 12px;
            max-width: 360px;
            @media screen and(max-width: 575px) {
                top: 61px;
                max-width: 100%;
            }
            .dropdown-item{
                font-size: px_to_rem(12px);
                font-weight: 700;
                line-height: 18px; 
                padding: 16px 20px;
                white-space: normal;
                &:not(:last-child){
                    border-bottom: 1px solid rgba(145, 158, 171, 0.24);
                }
                .icon-close{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 40px;
                    height: 40px;
                    padding: 8px;
                }
                &:active{
                    background: transparent;
                    color: inherit;
                }
                &.contact{
                    font-weight: 400;
                    color: var(--text-content);
                    font-size: px_to_rem(14px);   
                    line-height: 22px;
                    .title{
                        font-size: px_to_rem(16px);                    
                        font-weight: 600;
                        line-height: 24px;
                    }
                    .mail{
                        line-height: 22px; 
                    }
                    .block-social{
                        a{
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            width: 32px;
                            height: 32px;
                            padding: 6px;
                        }
                        [class^="icon"]{
                            
                            color: var(--color-social);
                            
                        }
                    }
                }
            }
        }
    }
    .list-group {
        max-height: 466px;
        overflow: auto;
        .list-group-title{
            font-size: 12px;
            font-weight: 700;
            line-height: 18px;
            text-transform: uppercase;
            border-bottom: 1px solid rgba(145, 158, 171, 0.24);
            padding: 8px 20px;
        }
        .list-group-item{
            padding: 12px 20px;
            border: 0;
            background-color: var(--text-white);
            &.unread{
                background-color: var(--bg-themes);
            }
        }
    }
}

.navbar-expand {
    .navbar-nav {
        .dropdown-menu{
            @media screen and (max-width: 575px) {
                top: 61px;
            }
        }
    }
}