/*Profile*/
.box-profile{
    .box-avatar{
        .thumb-art {
            width: 148px;
            height: 148px;
            margin: -74px auto 0;
        }
        .username{
            margin-top: 12px;
            font-size: px_to_rem(16px);
            font-weight: 600;
            line-height: 24px; 
            letter-spacing: 0.08px;
            color: var(--text-main);
        }
    }
}

/**/
.menu-profile{
    .title-box{
        padding: 24px 16px 8px 16px ;
        font-size: px_to_rem(12px);
        font-weight: 700;
        line-height: 18px; 
        text-transform: uppercase;
    }
    .menu-items{
        padding: 12px 12px 12px 16px;
        border-radius: 8px;
        &.active,
        &:hover{
            background: rgba(0, 171, 85, 0.08);
            cursor: pointer;
            [class*="icon"]{
                color: var(--btn-main);
            }
            .label{
                font-weight: 700;
                color: var(--btn-main)
            }
        }
    }
    .icon{
        font-size: 22px;
    }
    .label{
        color: var(--text-content);
    }
}

.settings-profile{
    @extend .list-payment;
    .list-item{
        .col-left{
            max-width: 100%;
        }
        .col-right{
            width: 100%;
            max-width: 68px;
        }
        .form-check-input{
            border-color: var(--text-content);
            &:checked[type=checkbox]{
                border-color: var(--btn-main);
            }
            &:focus{
                box-shadow: 0 0 0 0.25rem rgba(76, 221, 59, .25);
            }
        }
    }
}
