:root {
    /*Color Themes*/
    --bg-themes: #F9FAFB;
    --text-main: #212B36;
    --text-content: #637381;
    --text-content-2: #919EAB;
    --text-white: #FFFFFF;
    --text-green: #229A16;
    --bg-hover: #F4F6F8;
    --color-social: #00747B;
    --color-border: #E0E0E0;
    --text-error: #FF4842;
    --text-link: #007B55;

    /*Button Themes*/
    --btn-main: #00AB55;
    --btn-main-hover: #007B55;

    /*Font Themes*/
    --font-main: 'Public Sans';
}

/*Scrollbar Custom*/
%scroll {
    &::-webkit-scrollbar {
        width: 4px;
        height: 4px;
        background-color: transparent;
    }
    &::-webkit-scrollbar-thumb {
        background-color: #C4C4C4;
        border-radius: 10px;
    }
    &::-webkit-scrollbar-track {
        -webkit-box-shadow: inset 0 0 4px transparent;
        background-color: transparent;
    }
    scrollbar-width: thin;
    scrollbar-color: #C4C4C4 transparent;
}

@function px_to_rem($target, $context: $base-size) {
    @if $target==0 {
        @return 0
    }
    @return $target / $context+0rem;
}

$base-size: 16px;
$lh: 1.6;

/**Apply: "font-size: px_to_rem(16px)" or "font: 400 px_to_rem(16px) arial;" **/
