/*Payment*/

/*List Payment*/
.list-payment{
    .list-item{
        .col-left{
            font-weight: 500;
            line-height: 20px;
            letter-spacing: 0.07px;
            padding: 18px 22px;
            background: var(--bg-hover);
            border: 1px solid var(--bg-hover);
            border-bottom: 1px solid var(--text-white);
            width: 100%;
            max-width: 290px;
            @media screen and(max-width: 767px) {
                max-width: 100%;
                padding: 14px 18px;
            }
        }
        .col-right{
            width: 100%;
            line-height: 20px; 
            letter-spacing: 0.07px;
            color: var(--text-main);
            padding: 18px 22px;
            border: 1px solid var(--bg-hover);
            border-bottom: 0;
            @media screen and(max-width: 767px) {
                padding: 14px 18px;
                border: 0;
            }
        }
        &:last-child{
            .number{
                border-bottom: 1px solid var(--bg-hover);
            }
        }
    }
}

/*Pay Method*/
.payment-method{
    width: 100%;
    max-width: 575px;
    .gateway-payment{
        padding: 24px 10px;
        border-radius: 8px;
        border: 1px solid rgba(145, 158, 171, 0.32);
        &:hover{
            cursor: pointer;
        }
        &.active{
            .checkbox{
                background: var(--btn-main);
                border: 2px solid var(--btn-main);
                &:after{
                    opacity: 1;
                }
            }
        }
        .checkbox{
            padding: 2px;
            width: 22px;
            height: 22px;
            border: 2px solid var(--text-content);
            border-radius: 100%;
            display: flex;
            margin-right: 18px;
            margin-left: 12px;
            position: relative;
            transition: all .2s ease;
            &:after{
                border-bottom: 2px solid var(--text-white);
                border-left: 2px solid var(--text-white);
                content: "";
                height: 6px;
                left: 4px;
                position: absolute;
                top: 5px;
                transform: rotate(-45deg);
                width: 10px;
                opacity: 0;
                transition: all .2s ease;
            }
        }
        .name{
            font-weight: 700;
            line-height: 22px; 
        }
        .des{
            line-height: 22px; 
        }
    }
}

.box-VAT{
    .vat{
        font-size: px_to_rem(16px);
        font-weight: 600;
        line-height: 24px; 
        letter-spacing: 0.08px;
    }
}

