/*Campaign*/
.campaign-overview{
    .group{
        padding-bottom: 12px;
        margin-bottom: 12px;
        border-bottom: 1px dashed var(--bs-border-color);
        &.no-border{
            padding-bottom: 0;
            margin-bottom: 0;
            border-bottom: 0;
        }
        .title-group{
            font-weight: 600;
            line-height: 20px;
            letter-spacing: 0.07px;
        }
        .text{
            padding-left: 8px;
            &.sub{
                padding-left: 16px;
            }
        }
        .sub-title-group{
            font-size: px_to_rem(12px);
            font-weight: 600;
            line-height: 18px; 
            letter-spacing: 0.06px;
        }
    }
}

/*Create Campaign*/
.create-campaign{
    .block-create-campaign{
        .title{
            font-size: px_to_rem(16px);
            font-weight: 600;
            line-height: 24px; 
            letter-spacing: 0.08px;
        }
        .des{
            line-height: 20px; 
            letter-spacing: 0.07px;
            color: var(--text-content);
        }
    }
}

.intro-previews{
    .title{
        font-size: px_to_rem(16px);
        font-weight: 600;
        line-height: 24px;
        letter-spacing: 0.08px;
        color: var(--text-main);
    }
    .des{
        line-height: 20px; 
        letter-spacing: 0.07px;
        color: var(--text-content);
    }   
}

.box-previews{
    color: var(--text-main);
    .title{
        font-size:  px_to_rem(16px);
        font-style: normal;
        font-weight: 600;
        line-height: 24px; /* 150% */
        letter-spacing: 0.08px;
    }
    .title-box{
        font-weight: 700;
        line-height: 22px; 
        color: var(--text-content);
        margin-bottom: 8px;
    }
    .box-article{
        display: flex;
        .thumb-art{
            margin-right: 16px;
        }
        .title-article{
            font-family: 'Merriweather';
            font-weight: 700;
            line-height: 20px; 
            letter-spacing: 0.07px;
            margin-bottom: 8px;
        }
        .des{
            font-size: 12px;
            line-height: 20px;
            font-family: Arial;
            letter-spacing: 0.06px;
        }
    }
    .see-more{
        font-family: Arial;
        font-size: px_to_rem(12px);
        font-weight: 700;
        line-height: 20px; 
        letter-spacing: 0.06px;
        color: var(--text-content-2);
        margin-top: 4px;
    }
    &.format-pc{
        .box-article{
            .thumb-art{
                width: 175px;
            }
        }
    }
    &.promotion{
        .box-article{
            .thumb-art{
                width: 140px;
            }
        }
    }
    &.format-mb{
        width: 100%;
        max-width: 340px;
        .box-article{
            flex-direction: column;
            .thumb-art{
                margin-right: 0;
            }
        }
    }
    .box-qc{
        margin-top: 4px;
        color: var(--text-content);
        span{
            font-size: 10px;
            line-height: 20px; 
            letter-spacing: 0.05px;
            font-family: Arial;
        }
    }
}

/*Page Detail*/
.detail-campaign{
    .info-user{
        .text{
            color: var(--text-content);
            line-height: 20px;
            letter-spacing: 0.07px;
        }
    }
}

/*Box Load Image*/
.box-load-image{
    background: #454F5B;
    padding: 30px 80px;
    .box-image{
        width: 100%;
        min-height: 350px;
        position: relative;
        &:after{
            content:'';
            width: 100%;
            height: 60px;
            background: rgba(145, 158, 171, 0.80);
            position: absolute;
            top: 0;
            left: 0;
        }
        &:before{
            content:'';
            width: 100%;
            height: 90px;
            background: rgba(145, 158, 171, 0.80);
            position: absolute;
            bottom: 0;
            left: 0;
        }
    }
}

/*Option Image*/
.box-option-image{
    [class*="icon-"]{
        color: var(--btn-main);
    }
    .box-zoom{
        input[type="range"]{
            width: 100%;
        }
    }
    .box-rotate{
        .rotate-right{
            transform: scaleX(-1);;
        }
    }
}

.size-image{
    .size-item{
        position: relative;
        padding-left: 32px;
        &:before{
            content: '';
            width: 8px;
            height: 8px;
            border-radius: 100%;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 8px;
            background: var(--text-content);
        }
        &.choose{
            .icon-check{
                color: var(--btn-main);
                font-size: 24px;
                position: absolute;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
            }
            &:before{
                content: none;
            }
        }
        &.active{
            background: var(--bg-hover);
            padding: 4px 4px 4px 28px;
            border-radius: 8px;
        }
    }
}

/*Image Loaded*/

.image-loaded{
    &.size-16x9{
        .thumb-art{
            width: 100%;
            max-width: 448px;
        }
    }
    &.size-5x3{
        .thumb-art{
            width: 100%;
            max-width: 415px;
        }
    }
    &.size-4x3{
        .thumb-art{
            width: 100%;
            max-width: 332px;
        }
    }
    &.size-3x3{
        .thumb-art{
            width: 100%;
            max-width: 250px;
        }
    }
}