/*Modal Header*/
.modal-header{
    padding: 20px 24px;
    .modal-title{
        font-size: px_to_rem(24px);
        font-weight: 700;
        line-height: 36px; 
    }
}

.modal-themes{
    .title-modal{
        font-size: px_to_rem(18px);
        font-weight: 600;
        line-height: 28px;
        letter-spacing: 0.18px;
    }
    .modal-body{
        padding-bottom: 24px;
        .content-modal{
            line-height: 20px; 
            letter-spacing: 0.07px; 
            color: var(--text-content);
        }
    }
    .modal-footer{
        padding-top: 24px;
        justify-content: center;
        border-top: 1px solid var(--bg-hover);
        .info-support{
            font-size: px_to_rem(12px);
            line-height: 18px;
            color: var(--text-content);
        }
    }
}

.create-campain-success,
.create-campain-not-success{
    .modal-dialog{
        min-width: 640px;
    }
}

/*Upload Image*/
.modal-upload-image{
    .modal-dialog{
        --bs-modal-width: 990px;
    }
    .block-item{
        line-height: 22px; 
        color: var(--text-content);
        .title-block{
            font-weight: 700;
            color: var(--text-main);
        }
    }
    .btn-default{
        &:hover,
        &.active{
            color: var(--btn-main);
        }
    }
}