/*Input File Custom*/

.js .inputfile {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.inputfile + label {
    font-size: px_to_rem(14px);
    font-weight: 500;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: pointer;
    display: flex;
    align-items: center;
    overflow: hidden;
    padding: 1.25rem 2.5rem;
    border-radius: 8px;
    width: 100%;
    max-width: 420px;
}

.no-js .inputfile + label {
    display: none;
}

.inputfile:focus + label,
.inputfile.has-focus + label {
    outline: none;
}

.inputfile + label * {
    /* pointer-events: none; */
    /* in case of FastClick lib use */
}

.inputfile + label svg {
    width: 1em;
    height: 1em;
    vertical-align: middle;
    fill: currentColor;
    margin-top: -0.25em;
    /* 4px */
    margin-right: 0.25em;
    /* 4px */
}


/* style 2 */

.inputfile-2 + label {
    border: 1px dashed var(--btn-main);
    background: rgba(0, 171, 85, 0.08);
    .icon{
        flex-shrink: 0;
        font-size: 24px;
        color: var(--btn-main);
    }
    .text-upload{
        font-weight: 700;
        line-height: 22px;
        color: var(--btn-main);
    }
    .sub-text-upload{
        font-size: px_to_rem(12px);
        line-height: 18px;
        color: var(--text-content-2) 
    }
}

.inputfile-2:focus + label,
.inputfile-2.has-focus + label,
.inputfile-2 + label:hover {
    color: var(--text-content);
}


/*Form Template*/
.form{
    &.form-create-compaign{
        /* margin: 0 auto;
        max-width: 1600px; */
        .icon-info{
            color: var(--btn-main);
        }
    }
    textarea{
        &.form-control{
            height: auto;
        }
    }
    .box-daterangepicker{
        border-radius: 8px;
        border: 1px solid #C4CDD5;;
        box-shadow: none;
        .icon-calendar{
            left: auto;
            right: 14px;
            color: var(--text-content);
        }
        .form-control{
            padding: 8px 36px 8px 16px;
            height: 38px;
        }
    }
    .form-select,
    .form-control{
        height: 40px;
        border-radius: px_to_rem(8px);
        border-color: #C4CDD5;
    }
    .btn{
        padding: 8px 16px;
        box-shadow: none;
        /* &.btn-default{
            border: 1px solid var(--bg-hover);
            background: var(--bg-themes);
            color: var(--text-content);
        } */
    }
}

.block-title-form{
    border-bottom: 1px solid #F4F6F8;
}

.title-form{
    font-size: px_to_rem(18px);
    font-weight: 600;
    line-height: 28px; 
    letter-spacing: 0.18px;
    color: var(--text-main) !important;
}

.title-body{
    font-size: px_to_rem(16px);
    font-weight: 600;
    line-height: 24px;
    letter-spacing: 0.08px;
}

.content-body{
    line-height: 20px; 
    letter-spacing: 0.07px;
    color: var(--text-content);
}

.list-checkbox{
    width: 100%;
    max-width: 624px;
    &-inner{
        max-height: 208px;
        overflow-y: hidden;
        position: relative;
        &:after{
            content: '';
            width: 100%;
            height: 30px;
            background: linear-gradient(180deg, rgba(255, 255, 255, 0.00) 0%, #FFF 100%);
            position: absolute;
            left: 0;
            bottom: 0;
        }
    }
    &.active{
        .list-checkbox-inner{
            max-height: none;
            &:after{
                content: none;
            }
        }
        .view-more{
            display: none;
        }
    }
    .list-item{
        display: flex;
        align-items: center;
        width: 100%;
        position: relative;
        &:hover{
            cursor: pointer;
        }
        &.active{
            .box-dropdown-input{    
                opacity: 1;
                visibility: visible;
            }
            .form-check{
                border: 1px solid var(--btn-main);
            }
        }
    } 
    .form-check{
        width: 100%;
        display: flex;
        align-items: center;
        border-radius: 8px;
        padding: px_to_rem(8px);
        border: 1px solid #C4CDD5;
        position: relative;
        height: 40px;
        &:hover{
            cursor: pointer;
        }
        .form-check-input{
            margin: 0 8px 0 0;
        }
    }
    .box-dropdown-input{
        position: absolute;
        left: 0;
        top: 45px;
        width: 100%;
        background: var(--text-white);
        z-index: 1;
        padding: 20px;
        border-radius: 4px;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
        display: grid;
        grid-template-columns: repeat(2,1fr);
        grid-gap: 8px;
        opacity: 0;
        visibility: hidden;
        transition: all .3s ease;
        .item{
            padding: 8px;
        }
    }  
    &.list-inline{
        display: flex;
        .list-item{
            &:not(:last-child){
                margin-right: 16px;
            }
        }
    }
}

.text-error{
    color: var(--text-error);
}

.form-textarea{
    position: relative;
    .count-text{
        position: absolute;
        right: 8px;
        bottom: 8px;
    }
}

