.btn{
    padding: 6px 16px;
    height: 40px;
    border-radius: 8px;
    font-weight: 700;
    line-height: 24px; 
    &:first-child{
        &:active{
            border-color: transparent;
            background: #DFE3E8;
        }
    }
    /*Size Button*/
    &.btn-sm{
        padding: 4px 10px;
    }
    &.btn-lg{
        padding: 11px 22px;
    }
    &.btn-card{
        padding: 12px 16px;
        background: var(--text-white); 
        height: auto;
        display: flex;
        align-items: center;
        &:active{
            border: 1px solid transparent;
            background: var(--text-white);
        }
    }

    &.btn-default-form{
        border: 1px solid var(--bg-hover);
        background: var(--bg-themes);
        font-weight: 500;
        line-height: 20px;
        letter-spacing: 0.07px;
        &:active{
            border-color: transparent;
        }
        &:hover{
            background-color: #edf0f3
        }
    }
    &.btn-themes{
        padding: 8px 16px;
        &.btn-default{
            display: inline-flex;
            box-shadow: none;
            background: var(--bg-themes);
            border-color: transparent;
        }
    }

     /*Button Text*/
    &.btn-text{
        background: transparent;
        border: 0;
    }

    /*Button Icon*/
    .btn-icon{
        display: flex;
        align-items: center;
    }

    /*Status Button Template*/
    &.btn-main{
        color: var(--text-white);
        background: var(--btn-main);
        &:active{
            color: var(--text-white);
            background: var(--btn-main);
            border: 1px solid var(--btn-main);
        }
        &:hover{
            background: var(--btn-main-hover);
            border: 1px solid var(--btn-main-hover );
        }
        &.btn-text{
            color: var(--btn-main);
            &:hover{
                background: rgba(0, 171, 85, 0.08);
            }
        }
    }
    &.btn-default{
        background: #DFE3E8;
        box-shadow: 0px 8px 16px 0px rgba(145, 158, 171, 0.12);
        &:hover{
            background: #C4CDD5;
        }
        &.btn-text{
            background: transparent;
            color: var(--text-main);
            &:hover{
                background: rgba(145, 158, 171, 0.08);
            }
        }
    }
    &.btn-primary{
        border: 1px solid #36F;
        background: #36F;
        &:hover{
            background: #1939B7;
        }
        &.btn-text{
            border: 1px solid #36F;
            color: #3366FF;
            &:hover{
                background: rgba(51, 102, 255, 0.08);
            }
        }
    }
    &.btn-info{
        box-shadow: 0px 8px 16px 0px rgba(24, 144, 255, 0.24);
        background: #1890FF;
        &:hover{
            background: #0C53B7;
        }
        &.btn-text{
            color: #1890FF;
            &:hover{
                background: rgba(24, 144, 255, 0.08);
            }
        }
    }
    &.btn-success{
        box-shadow: 0px 8px 16px 0px rgba(84, 214, 44, 0.24);
        background: #54D62C;
        &:hover{
            background: #229A16;
        }
        &.btn-text{
            color: #54D62C;
            &:hover{
                background: rgba(84, 214, 44, 0.08);
            }
        }
    }
    &.btn-warning{
        box-shadow: 0px 8px 16px 0px rgba(255, 193, 7, 0.24);
        background: #FFC107;
        &:hover{
            background: #B78103;
        }
        &.btn-text{
            color: #FFC107;
            &:hover{
                background: rgba(255, 193, 7, 0.08);
            }
        }
    }
    &.btn-danger{
        box-shadow: 0px 8px 16px 0px rgba(255, 72, 66, 0.24);
        background: #FF4842;
        &:hover{
            background: #B72136;
        }
        &.btn-text{
            color: #FF4842;
            &:hover{
                background: rgba(255, 72, 66, 0.08);
            }
        }
    }

    /*Disabled*/
    &.disabled {
        color: rgba(145, 158, 171, 0.80);
        background: rgba(145, 158, 171, 0.24);
        border: 1px solid transparent;
        &.btn-text{
            color: rgba(145, 158, 171, 0.80);
        }
    }
}

/*Card Button*/
.card{
    .btn{
        border: 1px solid transparent;
        background: transparent;
        &:hover{
            background: rgba(145, 158, 171, 0.08);
        }
        &.btn-themes{
            &.btn-default{
                border: 1px solid var(--bg-hover);
                background:var(--text-white); 
            }
        }

        /*Status Button Template*/
        &.btn-main{
            color: var(--btn-main);
            border-color: var(--btn-main);
            /* &:active{
                color: var(--text-white);
                background: var(--btn-main);
                border: 1px solid var(--btn-main);
            } */
            &:hover{
                background: rgba(0, 171, 85, 0.08);
            }
            &.btn-text{
                border-color: transparent;
            }
        }
        &.btn-default{
            background: transparent;
            border: 1px solid rgba(145, 158, 171, 0.32);
            &:hover{
                background: rgba(145, 158, 171, 0.08);
            }
        }
        &.btn-primary{
            background: transparent;
            border: 1px solid #36F;
            color: #36F;
            &:hover{
                background: rgba(51, 102, 255, 0.08);
            }
        }
        &.btn-info{
            background: transparent;
            border: 1px solid #1890FF;
            color: #1890FF;
            &:hover{
                background: rgba(24, 144, 255, 0.08);
            }
        }
        &.btn-success{
            background: transparent;
            border: 1px solid #54D62C;
            color: #54D62C;
            &:hover{
                background: rgba(84, 214, 44, 0.08);
            }
        }
        &.btn-warning{
            background: transparent;
            border: 1px solid #FFC107;
            color: #FFC107;
            &:hover{
                background: rgba(255, 193, 7, 0.08);
            }
        }
        &.btn-danger{
            background: transparent;
            border: 1px solid #FF4842;
            color: #FF4842;
            &:hover{
                background: rgba(255, 72, 66, 0.08);
            }
        }

        /*Disabled*/
        &.disabled {
            background: transparent;
            border: 1px solid rgba(145, 158, 171, 0.24);
            color: rgba(145, 158, 171, 0.80);
        }
    }
}

/*Button Function*/
.btn-update-profile{
    border: 1px solid #E0E0E0;
    color: #757575;
}