* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    @extend %scroll;
}

html,
body {
    text-rendering: optimizeSpeed;
}
body {
    line-height: inherit;
    overflow-x: hidden;
    overflow-y: scroll;
    font-family: 'Public Sans';
    font-size: px_to_rem(14px);
    color: var(--text-main);
    &::-webkit-scrollbar {
        width: 8px;
        background-color: transparent;
    }
    &::-webkit-scrollbar-thumb {
        background-color: #C4C4C4;
        border-radius: 10px;
    }
    &::-webkit-scrollbar-track {
        -webkit-box-shadow: inset 0 0 8px transparent;
        background-color: transparent;
    }
    scrollbar-width: thin;
    scrollbar-color: #C4C4C4 transparent;
}

a:focus {
    outline: 1 !important;
}

a {
    color: inherit;
    text-decoration: none;
    outline: none;
    &:hover{
        text-decoration: none;
    }
}

summary {
    outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: inherit;
    font-weight: normal;
}

img {
    border: 0;
    font-size: 0;
    line-height: 0;
    max-width: 100%;
}

ul{
    padding-left: 0;
    margin-bottom: 0;
}

ul,
li {
    list-style-type: none;
}

table {
    border-collapse: collapse;
}

b, strong{
    font-weight: 700;
}

.main{
    background: var(--bg-themes);
    overflow: initial;
}

.card-body{
    box-shadow: 0px 8px 16px 0px rgba(145, 158, 171, 0.12);
}

.progress{
    background: #C8FACD !important;
    border-radius: 4px;
    height: 8px;
}

.progress-bar{
    height: 8px;
    border-radius: 4px;
}

/*Lock Menu Left*/
.sidebar{
    margin-left: -264px;
}

//Form

.form-control{
    &.text-link{
        &:focus{
            color: var(--text-link);
        }
    }
}

/*Input Checkbox*/
.form-check-input:checked{
    border-color: var(--btn-main);
    background-color: var(--btn-main);
}

/*Link Href*/
.link{
    color: #007B55;
}

/*Card*/
.card{
    color: var(--text-main);
}

/*Daterangepicker*/
.daterangepicker{
    td{
        &.active{
            background-color: #007B55;
            &:hover{
                background-color: #007B55;
            }
        }
    }
    .drp-buttons {
        .btn{
            height: auto;
        }
    }
    .btn{
        &.btn-primary{
            border-color: #007B55;
            background-color: #007B55;
            &:active{
                border-color: #007B55;
                background-color: #007B55;
            }
        }
    }
}

/*Alert*/
.alert{
    padding: 1rem;
}

/*Table*/
.table {
    tbody{
        border-color: var(--bg-hover);
	}
	td {
        color: var(--text-main);
		border-color: var(--bg-hover);
	}
	tfoot {
		border-color: var(--bg-hover);
	}
	th {
		border-color: var(--bg-hover);
	}
	thead {
		border-color: var(--bg-hover);;
	}
	tr {
		border-color: var(--bg-hover);
	}
	&>:not(:last-child)>:last-child>*{
		border-color: var(--bg-hover);
	}
}

@media(min-width: 1400px) {
    .container,.container-lg,.container-md,.container-sm,.container-xl,.container-xxl {
        max-width:1400px
    }
}