/*Custom Table*/
.table{
    thead{
        &.table-light{
            th{
                background-color: var(--bg-hover);
                color: var(--text-content);
            }
        }
        th{
            padding: px_to_rem(18px) px_to_rem(22px);
            border-color: var(--bg-hover);
        }
    }
    tbody{
        td{
            padding: px_to_rem(18px) px_to_rem(22px);
            border-color: var(--bg-hover);
        }
    }
}

/*Data Table*/
.data-table{
    .icon-action{
        font-size: 24px;
    }
    tbody{
        tr{
            &:nth-last-child(-n + 2){
                .box-dropdown-action{
                    .list-dropdown{
                        top: auto;
                        bottom: calc(100% + 4px);
                    }
                }
            }
        }
    }
    .box-dropdown-action{
        &:hover{
            .list-dropdown{
                opacity: 1;
                visibility: visible;
            }
        }
    }
}

/*Multitab Data Table*/
.multitab-data-table{
    display: flex;
    align-items: center;
    padding: 4px;
    border-radius: 8px;
    border: 1px solid var(--bg-hover);
    background: var(--text-white);
    overflow-x: auto;
    li{
        a{
            white-space: nowrap;
            padding: 6px 16px;
            border-radius: 8px;
            line-height: 24px;
        }
        &.active{
            a{
                font-weight: 700;
                background: #DFE3E8;
            }
        }
    }
}