footer{
    &.footer{
        padding-top: 24px;
        padding-bottom: 40px;
        .box-left{
            max-width: 290px;
        }
        .box-right{
            .title-list{
                font-size: 12px;
                font-style: normal;
                font-weight: 700;
                line-height: 18px; 
                text-transform: uppercase;
                li{
                    line-height: 22px; 
                }
            }
        }
        .block-social{
            display: flex;
            align-items: center;
            li{
                padding: 6px;
                [class*="icon"]{
                    color: var(--color-social);
                }
            }
        }
        .copyright{
            padding-top: 20px;
            margin-top: 20px;
            border-top: 1px solid var(--color-border);
        }
    }
}
