/* Scss Document */
.thumb_art{
    position: relative;
    margin-right: 10px;
    float: left;
    width: 140px;
    margin-bottom: 10px;
}
.thumb{
    display: block;
    overflow: hidden;
    height: 1px;
    position: relative;
    width: 100%;
    background: #f4f4f4;
    border-radius: 8px;
    img{
        width: 100%;
        height: 100%;
        float: left;
        object-fit: cover;
        object-position: center;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
    }
    iframe{
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
    }
}
.thumb_5x3 {
    padding-bottom: 60%;
}
.thumb_4x3 {
    padding-bottom: 75%;
}
.thumb_3x3 {
    padding-bottom: 100%;
}
.thumb_5x2 {
    padding-bottom: 40%;
}
.thumb_3x2 {
    padding-bottom: 66.66667%;
}
.thumb_16x9 {
    padding-bottom: 56.25%;
}
.thumb_1x1 {
    padding-bottom: 100%;
}
.thumb_video{
    width: 100%;
    height: 0;
    padding-bottom: 56.25%;
    margin-bottom: 0;
    position: relative;
    iframe{
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
    }
}
.thumb-icon{
    width: 25px;
    height: 25px;
    background: rgba(246,136,33,0.7);
    display: inline-block;
    position: absolute;
    left: 0;
    bottom: 0;
    svg{
        fill:#fff;
        position: relative;
        left: 5px;
        top: 4px;
        &.ic-camera{
            width: 13px;
        }
    }
}
